@use "../global/breakpoints" as b;
@import "./variables";
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "poppins", sans-serif;
}

/* scroll bar */
::-webkit-scrollbar {
  width: 3px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #1013201d;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: $heading;
  border-radius: 10px;
}

body {
  max-width: 100vw;
  min-height: 100vh;
  background-color: $base-background !important;
  overflow-x: hidden;
}
#root {
  color: $base-color !important;
}

.pages {
  width: 100vw;
  // height: 100vh;
  margin: 0 auto;
  // border: 1px dashed black;
}

.home,
.explore,
.notification,
.bookmark,
.profil-page,
.settings-page,
.follow,
.suggetions {
  width: 1300px;
  height: 100vh;
  margin: 0 auto;

  @include b.breakpoint("1300") {
    width: 90%;
  }
  @include b.breakpoint("700") {
    width: 100%;
  }
}

a {
  text-decoration: none;
  color: black;
}

.notification-alert {
  background-color: white;
  color: black;
  position: fixed;
  top: 3%;
  right: 2%;
  width: fit-content;
  padding: 0.7rem 1rem 0.7rem 1rem;
  border-radius: 10px 10px 0 10px;
  border-bottom: 0.3rem solid $heading;
  z-index: 20000;
  box-shadow: 0 0 12px 12px rgba(20, 20, 20, 0.09);
}
